.home {
    display: flex;
    position: relative;
    height: 40%;
    width: 100%;
    align-items: center;
    align-self: center;
    align-content: center;
    justify-content: center;
    background-color:#ECD8CF;
    text-align: center;
}

a {
    text-decoration: none;
}

.bgimage {
    display: flex;
    position: relative;
    height: 100%;
    width: 100%;
    border-radius: 18em/2em;}

.HomeText {
    display: grid;
    font-family: monospace;
    position: absolute;
    z-index: 1;
    font-size: xx-large;
    transform: translate(-130%, -50%);
    text-align: left;
    justify-content: center;
}

.title {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    color: #ECD8CF;
    font-size: 60px;
}

.description {
    font-family: monospace;
    color: aliceblue;
    font-size: large;
    font-size: xx-large;
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    align-content: center;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    color: #1C5C4D;
    background-color: #ECD8CF;
    height: 50px;
    width: 100%;
    border: 5px solid #1C5C4D;
    margin: 50px 0px 0px 0px;
    border-radius: 20px;
    padding: 40px 30px 40px 30px;
    font-size: x-large;
    box-shadow:  0 0 5px 5px rgba(0,0,0,0.2);
}

.button:hover {
    border: 5px solid #ECD8CF;
    color: #ECD8CF;
    background-color: #1C5C4D;
    transition: all 0.5s ease;
    box-shadow:  0 0 10px 10px rgba(0,0,0,0.2);
}

.ExtraDetes {
    color: #ECD8CF;
    display: grid;
    font-family: monospace;
    position: absolute;
    box-shadow: inset 0 0 0 500px rgba(0,0,0,0.2);
    z-index: 1;
    font-size: x-large;
    transform: translate(130%, -50%);
    text-align: center;
    justify-content: center;
    border: 5px solid #ECD8CF;
    border-radius: 20px;
}

.ExtraDetesU {
    border-bottom: 5px solid #ECD8CF;
    border-radius: 20px 20px 0px 0px;
    padding: 10px 20px 10px 10px;
}

.ExtraDetesL {
    padding: 0px 10px 0px 20px;
}


.ExtraDetesI {
    display: flex;
    margin: 10px 0px 10px 0px;
}

.ExtraDetesT {
    margin-left: 10px;
}

@media screen and (max-width:1500px) {
    .home {
        display: flex;
        position: relative;
        height: 700px;
        width: 100%;
        align-items: center;
        align-self: center;
        align-content: center;
        justify-content: center;
        background-color:#ECD8CF;
        text-align: center;
        overflow-x: hidden;
    }
    .bgimage {
        display: flex;
        position: relative;
        height: 100%;
        width: fit-content;
        border-radius: 18em/2em;
        margin-left: 20px;
    }

    .HomeText {
        justify-content: center;
        text-align: center;
        transform: translate(0%, -40%);
    }

    .description {
        font-size: large;
    }

    .title {
        font-size: 60px;
    }

    a {
        width: auto;
        display: flex;
        justify-content: center;
        text-decoration: none;
    }

    .button {
        margin: 10px;
        font-size: 20px;
        height: 10%;
        width: 70%;
    }

    .ExtraDetes {
        margin-top: 100px;
        font-size: large;
        transform: translate(0%, 60%);
    }

    .ExtraDetesI {
        display: flex;
        margin: 10px 0px 10px 0px;
    }
    .ExtraDetesT {
        margin: 0px 10px 0px 10px;
    }


}